.home-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    background-color: #1c1b1b;
}
/* put clock at top right corner */
.clock {
    position: absolute;
    top: 10px;
    right: 10px;
}