.time {
  display: flex;
  height: 100%;
}

.digit {
  height: 100%;
  width: 100%;
}

@media {
  .image-change {
    animation: smooth-change 1s infinite;
  }
}
@keyframes smooth-change {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(360deg);
  }
}
